/*! Drupal visually-hidden CSS class that hides content from visual users and
   shows content to screen reader users. */
@mixin visually-hidden {
  position: absolute !important;
  overflow: hidden;
  clip: rect(1px, 1px, 1px, 1px);
  inline-size: 0.063rem;
  block-size: 0.063rem;
  word-wrap: normal;
}

// Handles thick border above fields in main DH content.
@mixin divider {
  padding-block-start: var(--v-space-2xs);
  margin-block-start: var(--v-space-2xs);
  border-block-start: 5px solid var(--mukurtu-view-row-divider-color);
}

// Comma-separated list for taxonomy fields with multiple values.
@mixin inline-list {
  .field__item {
    display: inline;

    &::after {
      content: ', ';
    }

    &:last-of-type {
      &::after {
        content: '';
      }
    }
  }
}

@mixin button {
  color: var(--light-text-color);
  background: var(--brand-primary-dark);
  font-size: var(--font-size-base);
  font-weight: var(--text-bold);
  letter-spacing: var(--letter-spacing-label);
  border: none;
  padding-block: var(--v-space-5xs);
  padding-inline: var(--v-space-2xs);
  cursor: pointer;

  &:hover,
  &:active,
  &:focus {
    color: var(--light-text-color);
    background: var(--brand-primary-accent);
  }

  &:focus {
    outline: 2px solid var(--focus-color);
  }

  &:disabled {
    color: var(--light-text-color);
    background: var(--brand-text-color);
  }
}

@mixin block-link {
  background-color: var(--color-white);
  border: 1px solid var(--brand-primary-dark);
  color: var(--brand-primary-dark);
  font-weight: var(--text-bold);
  line-height: normal;
  text-decoration: none;
  padding-block: var(--v-space-6xs);
  padding-inline: var(--v-space-5xs);
  letter-spacing: normal;

  &:hover {
    background-color: var(--brand-primary-dark);
    color: var(--color-white);
  }

  &:focus {
    outline-offset: 1px;
  }
}

@mixin button-arrow {
    position: relative;
    display: block;
    inline-size: max-content;
    max-inline-size: 75%;
    z-index: 2;

    @include button;

    &::after {
      content: '';
      display: inline-block;
      background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" xml:space="preserve" width="20" height="30" viewBox="5 -5 20 30"><path fill="white" d="M25 10.01 9.72 25l-4.53-4.43L15.78 10.2 5-.37 9.72-5 25 10h-.01z"/></svg>');
      background-repeat: no-repeat;
      background-size: contain;
      inline-size: 0.6em;
      block-size: 1em;
      vertical-align: middle;
      margin-inline-start: 2ch;
    }

    &:hover {
      background-color: var(--brand-primary);
      text-decoration: underline;
    }
}

@mixin block-link-active {
  background-color: var(--brand-primary-dark);
  color: var(--color-white);

  &:hover {
    background-color: var(--brand-primary-accent);
  }
}
